.nav {
    grid-area: nav;
    background-color: green;
    padding: 40px 0;
    display: grid;
    grid-column: auto;
    row-gap: 15px;
    align-content: start;
    justify-content: center;
    text-align: center;
    font-size: 20px;
    letter-spacing: 1px;
    height: 310px;
}

.item {
    text-decoration: none;
    color: #fff;
    cursor: pointer;
    /* width: 100px; */
}

a.item.activeLink {
    /* color: rgb(255, 0, 0); */
    font-weight: 700;
}
